/* eslint-disable @next/next/no-img-element */
"use client"

import * as React from "react"
import { useState, useEffect } from "react"
import { usePathname } from "next/navigation"
import {
  IconChartBar,
  IconHome,
  IconReport,
  IconTransfer,
  IconUser,
  IconUserPlus,
} from "@tabler/icons-react"

import { NavMain } from "@/components/nav-main"
import { NavSecondary } from "@/components/nav-secondary"
import { NavUser } from "@/components/nav-user"
import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarHeader,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
} from "@/components/ui/sidebar"
import { useTheme } from "next-themes"

export function AppSidebar({ ...props }: React.ComponentProps<typeof Sidebar>) {
  const pathname = usePathname()
  const { theme } = useTheme()
  const [userEmail, setUserEmail] = useState('')
  const [userFirstName, setUserFirstName] = useState('')
  const [userLastName, setUserLastName] = useState('')
  const [userRole, setUserRole] = useState('')
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
    const storedEmail = localStorage.getItem('email')
    const storedFirstName = localStorage.getItem('firstName')
    const storedLastName = localStorage.getItem('lastName')
    const storedRole = localStorage.getItem('role')
    if (storedEmail) {
      setUserEmail(storedEmail)
    }
    if (storedFirstName) {
      setUserFirstName(storedFirstName)
    }
    if (storedLastName) {
      setUserLastName(storedLastName)
    }
    if (storedRole) {
      setUserRole(storedRole)
    }
  }, [])

  const isActive = (url: string) => {
    if (url === "#") return false
    return pathname === url
  }

  const hasActiveChild = (items?: { title: string, url: string }[]) => {
    if (!items) return false
    return items.some(item => pathname.startsWith(item.url))
  }

  const data = {
    user: {
      name: userFirstName + ' ' + userLastName,
      email: userEmail,
      avatar: ''
    },
    navMain: userRole === 'ROLE_ANALYST' ? [
      {
        title: "Home Page",
        url: "/home-page",
        icon: IconHome,
        isActive: isActive("/home-page"),
      },
      {
        title: "Reports",
        url: "/reports",
        icon: IconReport,
        isActive: isActive("/reports"),
      }
    ] : [
      {
        title: "Home Page",
        url: "/home-page",
        icon: IconHome,
        isActive: isActive("/home-page"),
      },
      {
        title: "Customers",
        url: "/customers",
        icon: IconUser,
        isActive: isActive("/customers"),
      },
      ...(userRole !== 'ROLE_TRADER' ? [
        {
          title: "Employees",
          url: "/employees",
          icon: IconUserPlus,
          isActive: isActive("/employees"),
        }
      ] : []),
      {
        title: "Buy-Sell",
        url: "#",
        icon: IconChartBar,
        isActive: hasActiveChild([
          { title: "Order History", url: "/buy-sell/order-history" },
          { title: "Stock Definition", url: "/buy-sell/stock-definition" },
          { title: "Quick Buy/Sell", url: "/buy-sell/quick-buy-sell" },
          { title: "Internal Transfers", url: "/buy-sell/internal-transfers" },
        ]),
        items: userRole === 'ROLE_TRADER' ? [
          {
            title: "Quick Buy/Sell",
            url: "/buy-sell/quick-buy-sell",
          }
        ] : userRole === 'ROLE_ADMIN' ? [
          {
            title: "Order History",
            url: "/buy-sell/order-history",
          }
        ] : [
          {
            title: "Order History",
            url: "/buy-sell/order-history",
          },
          {
            title: "Quick Buy/Sell",
            url: "/buy-sell/quick-buy-sell",
          }
        ]
      },
      ...(userRole !== 'ROLE_TRADER' ? [
        {
          title: "Reports",
          url: "/reports",
          icon: IconReport,
          isActive: isActive("/reports"),
        }
      ] : []),
      {
        title: "Operational Transactions",
        url: "#",
        icon: IconTransfer,
        isActive: hasActiveChild([
          { title: "Stock Transfers", url: "/operational/stock-transfers" },
          { title: "Cash Transfers", url: "/operational/cash-transfers" },
          { title: "End of Day", url: "/operational/end-of-day" },
        ]),
        items: [
          {
            title: "Stock Transfers",
            url: "/operational/stock-transfers",
          },
          {
            title: "Cash Transfers",
            url: "/operational/cash-transfers",
          },
          {
            title: "End of Day",
            url: "/operational/end-of-day",
          }
        ]
      }
    ],
    navClouds:[],
    navSecondary: []
  }

  return (
    <Sidebar collapsible="offcanvas" {...props}>
      <SidebarHeader>
        <SidebarMenu>
          <SidebarMenuItem>
            <SidebarMenuButton
              asChild
              className="data-[slot=sidebar-menu-button]:!p-1.5"
            >
              <a href="/home-page">
                <img
                  src={!mounted || theme === "light" ? "/logo.png" : "/logo-light.png"}
                  alt="Logo"
                  className="size-8"
                />
                <span className="text-base font-semibold">Fintra Stock Trading</span>
              </a>
            </SidebarMenuButton>
          </SidebarMenuItem>
        </SidebarMenu>
      </SidebarHeader>
      <SidebarContent>
        <NavMain items={data.navMain} />
        <NavSecondary items={data.navSecondary} className="mt-auto" />
      </SidebarContent>
      <SidebarFooter>
        <NavUser user={data.user} />
      </SidebarFooter>
    </Sidebar>
  )
}
